body {
  width: 85%;
  background: #fbe9da;
  font-family: MetricWeb;
  margin: 0 auto;
}

.n-storylines__scroll {
  overflow-x: auto;
  white-space: nowrap;
  display: flex;
  position: relative;
  background: #fbe9da;
}

.n-storylines__all-months {
  min-width: 50px;
}

.dot-container {
  list-style-type: none;
  padding: 0;
  margin: 0;
  position: relative;
  margin-right: 20px;
}

.dot {
  position: absolute;
  background: black;
  top: -10px;
  height: 10px;
  width: 10px;
  border-radius: 100%;
  text-align: center;
  font-size: 10px;
  line-height: 10px;
  color: white;
  padding: 5px;
  z-index: 3;
  margin-right: -4%;
}

.n-storylines {
  border-top: 8px solid #000;
  border-bottom: 1px solid #cec6b9;
}

.n-storylines__timeline-container {
  display: flex;
  position: relative;
  width: 100%;
  background: #fbe9da;
}

.n-storylines__timeline-container:before {
  border-top: 1px solid #cec6b9;
  content: "";
  margin: 0 auto;
  position: absolute;
  top: 35px;
  left: -30%;
  width: 150%;
  z-index: -1;
}

.n-storylines__heatmap-segment {
  width: 100%;
  margin-top: 10px;
  text-align: center;
}

.n-storylines__heatmap-segment-colour,
.n-storylines__heatmap-segment-none {
  background: #B11F42;
  width: 100%;
  height: 50px;
  display: flex;
}

.n-storylines__heatmap-segment-name {
  position: relative;
  margin-top: 10px;
}

.n-storylines__heatmap-segment-name::before {
  counter-increment: dot;
  content: '';
  position: absolute;
  top: -95%;
  left: 45%;
  background-color: #fbe9da;
  border: 1px solid rgba(0, 0, 0, 0.6);
  border-radius: 100%;
  height: 9px;
  width: 9px;
}

.n-storylines__article {
  position: relative;
  margin: -5px 0 25px;
  font-size: 18px;
  line-height: 20px;
}

.n-storylines__article::before {
  counter-increment: list;
  content: counter(list);
  position: absolute;
  font: 14px/15px MetricWeb, sans-serif;
  color: #fff1e0;
  left: -35px;
  top: 2px;
  text-align: center;
  background-color: #333;
  border-radius: 100%;
  height: 15px;
  width: 15px;
  padding: 5px;
}

.n-storylines__top-content {
  counter-reset: list;
  list-style-type: none;
  width: 85%;
  margin-top: 20px;
  position: relative;
}

.n-storylines__top-content::before {
  content: '';
  position: absolute;
  border-left: 1px solid #cec6b9;
  top: 5px;
  bottom: 35px;
  left: 17px;
  margin: 0;
}

.n-storylines__info {
  margin: 8px 0;
}

.n-storylines__content-count {
  float: left;
  margin-bottom: 10px;
}

.no-articles {
  opacity: 0.6;
}

.n-storylines__article-header {
  margin-top: 40px;
}

span {
  font-weight: 600;
}

.n-storylines__article > .o-typography-timestamp {
  margin-top: 5px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.6);
}

.n-storylines__article-link {
  text-decoration: none;
  color: #505050;
}

.n-storylines__article-link:focus {
  cursor: pointer;
  color: #777;
}

.n-storylines__back-btn {
  width: 1.5em;
  height: 4.5em;
}



